
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import { mapMutations, mapState } from 'vuex';
export default {
components: {
XButton, Group, XInput
},
data() {
return {
showScrollBox: true,
userName: '',
userPwd: '',
registUserName: '',
registUserPwd: '',
isLoginContent: true
}
},
methods: {
...mapMutations({
changeLogin: "changeLogin",
changeUserInfo: "changeUserInfo"
}),
login() {
var params = {
userName: this.userName,
userPwd: this.userPwd
}
if(params.userName && params.userPwd){
this.$http.post("http://www.69212222.com:8080/housekeep/vip/login", params)
.then(res => {
console.log(res);
this.userName = '';
this.userPwd = '';
if (res.data.success) {
this.changeUserInfo({ userInfo: res.data.data });
this.$vux.toast.show({
text: res.data.msg
})
this.changeLogin({ isLogin: true });
} else {
this.$vux.toast.show({
text: res.data.msg,
type: "warn"
})
}
})
.catch(res => {
console.log(res);
})

}else{
this.$vux.toast.show({
type: "warn",
text:"不能为空"
});
}

},
regist() {
var params = {
userName: this.userName,
userPwd: this.userPwd
}
if(params.userName && params.userPwd){
this.$http.post("http://www.69212222.com:8080/housekeep/vip/register", params)
.then(res => {
this.userName = '';
this.userPwd = '';
console.log(res);
if (res.data.success) {
this.changeUserInfo({ userInfo: res.data.data });
this.$vux.toast.show({
text: res.data.msg
})
this.changeLogin({ isLogin: true });
} else {
this.$vux.toast.show({
text: res.data.msg,
type: "warn"
})
}
})
.catch(res => {
console.log(res);
})
}else{
this.$vux.toast.show({
type: "warn",
text:"不能为空"
});
}

},
unLogin() {
this.changeLogin({ isLogin: false });
}
},
computed: {
...mapState({
isLogin: state => state.isLogin,
userInfo: state => state.userInfo
})
}
}




